	export default {
        created() {
            this.get_data();
        },
		data() {
			return {
				signData:[]
			};
		},
        methods:{
            sign(){
                this.$u.post('/sign').then(res=>{
                    // uni.showToast({
                    //     title:res.msg,
                    //     icon:res.msg==='签到成功'?'success':'none'
                    // })
                    if(res.data){
                       uni.showToast({
                        title:res.data,
                        icon:'none',
                        duration:2000
                    }) 
                     this.get_data();
                    }
                    
                })
            },
           async get_data(){
                await this.$u.post('/sign_data').then(res=>{
                    if(res.errorCode!==0)return;
                    this.signData=res.data
                })
            }
        }
	}
